export default {
  data() {
    return {
      fileList: [
        {
          name: "food.jpeg",
          url: "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100",
        },
        {
          name: "food2.jpeg",
          url: "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100",
        },
      ],
      input: "",
      primaryValue: "",
      secondValue: "",
      primaryClassification: [],
      secondClassification: [],
      textarea: "",
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleSuccess(esponse, file, fileList) {
      console.log(esponse, file, fileList);
    },
    primarySelectChange() {
      let params = new URLSearchParams();
      params.append("primaryClassification", this.primaryValue);
      this.axios
        .post("/getSecondClassificationByPrimaryClassification", params)
        .then((res) => {
          let result = res.data;
          if (result.status) {
            this.secondClassification = result.list;
          } else {
            this.$notify.error({
              title: "错误",
              message: result.message,
              duration: 0,
            });
          }
        })
        .catch((err) => {
          this.$notify.error({
            title: "错误",
            message: err,
            duration: 0,
          });
        });
    },
    handleCommit(){
      let data = new FormData();
      // data.append(')
    }
  },
  created() {
    this.axios
      .get("/primaryClassification/getAll")
      .then((res) => {
        this.primaryClassifition = res.data;
      })
      .catch((err) => {
        this.$notify.error({
          title: "错误",
          message: err,
          duration: 0,
        });
      });
  },
};



